.preview {
  position: relative;
}
.preview .school-header {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}
.preview .school-header .nav-link {
  color: white;
}
.preview-block {
  position: relative;
  background-image: url("/img/school-prewiev-pro.jpeg");
  background-size: no-repeat;
  background-position: 35% 65%;
  background-size: cover;
  width: 100%;
  height: 650px;
  background-attachment: fixed;
  -webkit-filter: blur(1.5px);
          filter: blur(1.5px);
}
@media (max-width: 900px) {
  .preview-block {
    background-position: 25% 75%;
  }
}
@media (max-width: 650px) {
  .preview-block {
    background-position: 15% 85%;
  }
}
@media (max-width: 320px) {
  .preview-block {
    height: 600px;
  }
}
.preview-block__title {
  color: #fff;
  position: absolute;
  top: 100px;
  left: 30px;
  max-width: 650px;
}
@media (max-width: 320px) {
  .preview-block__title {
    left: 20px;
    top: 175px;
    height: 450px;
  }
  .preview-block__title h1 {
    font-size: 20px;
  }
}
.preview-block__title span {
  color: #ffa6d7;
}
.preview-block__title p {
  font-size: 14px;
  width: 40%;
}
@media (max-width: 650px) {
  .preview-block__title p {
    width: 70%;
  }
}
.preview-block__title button {
  color: #fff;
}

.astro-links__subtitle {
  font-size: 16px;
  width: 100%;
  padding: 40px 80px;
  border: 1px solid #ffa6d7;
  border-radius: 10px;
  -webkit-box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
}
.astro-links .btn-light {
  -webkit-box-shadow: none;
          box-shadow: none;
  border: 1px solid #ffa6d7;
}

.condition .tilesWrap {
  padding: 0;
  margin: 50px auto;
  list-style: none;
  text-align: center;
}
.condition .tilesWrap li {
  display: inline-block;
  width: 75%;
  min-width: 200px;
  max-width: 320px;
  padding: 80px 20px 40px;
  position: relative;
  vertical-align: top;
  margin: 10px;
  font-family: "helvetica", san-serif;
  min-height: 45vh;
  background: #ffffff;
  border: 1px solid #252727;
  text-align: left;
}
.condition .tilesWrap li p {
  font-size: 16px;
  line-height: 18px;
  color: #000000;
  margin-top: 5px;
  width: 85%;
}
.condition .tilesWrap li:before {
  content: "";
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  z-index: -1;
  background: #fff;
  -webkit-transform: skew(2deg, 2deg);
      -ms-transform: skew(2deg, 2deg);
          transform: skew(2deg, 2deg);
}
.condition .tilesWrap li:after {
  content: "";
  position: absolute;
  width: 40%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgba(255, 255, 255, 0.02);
}
.condition .tilesWrap li:nth-child(1):before {
  background: #c9ffbf;
  background: -webkit-gradient(linear, left top, right top, from(#ffafbd), to(#c9ffbf));
  background: -o-linear-gradient(left, #ffafbd, #c9ffbf);
  background: linear-gradient(to right, #ffafbd, #c9ffbf);
}
.condition .tilesWrap li:nth-child(2):before {
  background: #f2709c;
  background: -webkit-gradient(linear, left top, right top, from(#ff9472), to(#f2709c));
  background: -o-linear-gradient(left, #ff9472, #f2709c);
  background: linear-gradient(to right, #ff9472, #f2709c);
}
.condition .tilesWrap li:nth-child(3):before {
  background: #c21500;
  background: -webkit-gradient(linear, left top, right top, from(#ffc500), to(#c21500));
  background: -o-linear-gradient(left, #ffc500, #c21500);
  background: linear-gradient(to right, #ffc500, #c21500);
}
.condition .tilesWrap li:nth-child(4):before {
  background: #fc354c;
  background: -webkit-gradient(linear, left top, right top, from(#0abfbc), to(#fc354c));
  background: -o-linear-gradient(left, #0abfbc, #fc354c);
  background: linear-gradient(to right, #0abfbc, #fc354c);
}

.card {
  -webkit-box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
}

